<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <v-counter :max="20" :min="1" @on-change="onParamChange('buyNum',$event)"></v-counter>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                 <v-selection :selections="buyTypes" @on-change="onParamChange('buyType',$event)"></v-selection>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                <v-choose :selections="periodList"  @on-change="onParamChange('period',$event)"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
              <v-mul-choose :selections="versionList"@on-change="onParamChange('versions',$event)"></v-mul-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                  {{ price }} 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <my-dialog :is-show='isShowPayDialog' @on-close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ period.label }}</td>
            <td>
              <span v-for="item in versions">{{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-chooser @on-change="onChangeBanks"></bank-chooser>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
       <check-order :is-show-check-dialog="isShowCheckOrder" :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
  </div>
</template>

<script>
import VSelection from '../../components/base/selection.vue'
import VChoose from "../../components/base/chooser.vue"
import VCounter from '../../components/base/counter.vue'
import VMulChoose from '../../components/base/multiplyChooser.vue'
import Dialog from '../../components/dialog'
import BankChooser from '../../components/bankChooser.vue'
import CheckOrder from '../../components/checkOrder'
import _ from 'lodash'
export default {
  components:{
      VSelection,
      VCounter,
      VChoose,
      VMulChoose,
      MyDialog:Dialog,
      BankChooser,
      CheckOrder
  },
  data () {
    return {
      buyNum: 0,
      buyType: {},
      versions: [],
      period: {},
      price: 0,
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      periodList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      buyTypes: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      isShowPayDialog: false,
      bankId: null,
      orderId: null,
      isShowCheckOrder: false,
      isShowErrDialog: false
    }
  },
  methods:{
    onParamChange(attr,val){
      this[attr]=val
      this.getPrice()

    },
    getPrice(){
      let buyVersionsArray = _.map(this.versions,(item)=>{
        return item.value
      })
      let reqParams = {
        buyNumber:this.buyNum,
        buyType:this.buyType.value,
        period:this.period.value,
        version:buyVersionsArray.join(',')
      }
      this.$http.post('/api/getPrice',reqParams).then((res)=>{
        this.price = res.data.amount;
      })
    },
    showPayDialog(){
      this.isShowPayDialog = true
    },
    hidePayDialog(){
      this.isShowPayDialog = false
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
    onChangeBanks(bankObject){
      this.bankId = bankObject.id
      console.log(this.bankId );
    },
    confirmBuy(){
      let buyVersionsArray = _.map(this.versions,(item)=>{
        return item.value
      })
      let reqParams = {
        buyNumber:this.buyNum,
        buyType:this.buyType.value,
        period:this.period.value,
        version:buyVersionsArray.join(','),
        bankId:this.bankId
      }
      this.$http.post('/api/createOrder',reqParams).then((res)=>{
        this.orderId = res.data.orderId
        this.isShowCheckOrder = true
        console.log(this.isShowCheckOrder,this.orderId )
        this.showPayDialog = false
      },(err)=>{
         this.isShowBuyDialog = false
        this.isShowErrDialog = true
      })
    }
  },
  mounted(){
    this.buyNum = 1
    this.buyType = this.buyTypes[0]
    this.versions = [this.versionList[0]]
    this.period = this.periodList[0]
    this.getPrice()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>



